
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'LoaderGlobal',
  props: {
    bar: {
      type: Boolean,
      default: true,
    },
    spinner: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      loading: false,
    };
  },
  computed: {
    routeLoading(): boolean {
      return this.$store.state.app.routeLoading;
    },
  },
  watch: {
    routeLoading(isLoading: boolean) {
      if (isLoading) {
        this.loading = true;
      } else {
        this.loading = false;
      }
    },
  },
});
