
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'Zone',
  props: {
    theme: {
      type: String,
      default: '',
    },
  },
  computed: {
    classes() {
      return {
        zone: true,
        'zone--with-bg': this.theme,
        ['theme-' + this.theme]: this.theme,
      };
    },
  },
});
