
import { defineComponent } from 'vue';
import BaseIconAsync from '@/components/base/BaseIconAsync.vue';

export default defineComponent({
  name: 'SectionHeader',
  components: {
    BaseIconAsync,
  },
  props: {
    headingLevel: {
      type: String,
      default: 'h2',
    },
    title: {
      type: String,
      required: true,
    },
    subtitle: {
      type: String,
    },
    to: {
      // routerlink :to
      type: [String, Object],
    },
    ctaLabel: {
      type: String,
    },
    icon: {
      type: String,
    },
  },
});
