
import { Toast } from '@/store/toast/types';
import { defineComponent, PropType } from 'vue';

export default defineComponent({
  name: 'Toast',
  data() {
    return {
      showing: false,
      timer: null as null | number | undefined,
    };
  },
  props: {
    notification: {
      type: Object as PropType<Toast>,
      required: true,
    },
  },
  computed: {
    notificationClass(): string {
      return 'toast--' + this.notification.theme;
    },
  },
  watch: {
    notification() {
      // existing notification modified
      if (this.showing) {
        clearTimeout(this.timer as number);
      }

      this.showing = true;

      this.timer = setTimeout(() => {
        this.showing = false;
        this.timer = null;
        this.$emit('done');
      }, this.notification.duration);
    },
  },
  methods: {
    dismiss() {
      if (this.notification.dismissable) {
        this.showing = false;
        clearTimeout(this.timer as number);
        this.$emit('done');
      }
    },
    handleAction() {
      if (this.notification.action) {
        this.notification.action();
      }
    },
  },
  created() {
    this.showing = true;

    this.timer = setTimeout(() => {
      this.showing = false;
      this.timer = null;
      this.$emit('done');
    }, this.notification.duration);
  },
  beforeUnmount() {
    clearTimeout(this.timer as number);
  },
});
